@font-face {
    font-family: 'HarryPotter';
    src: url('../public/font/HarryPotter.ttf');
}
* {
    padding: 0;
    margin: 0;
}
.allWrap {
    height: auto;
}
.tabWrap {
    display: flex;
    justify-content: center;
    padding-top: 120px;
}
div[class^='tabBtn'] {
    width: 45px;
    height: 64px;
    margin: 0 15px;
    cursor: pointer;
}
.tabBtn01 {
    background: url('../public/images/tab/tab01.png');
}
.tabBtn02 {
    background: url('../public/images/tab/tab02.png');
}
.tabBtn03 {
    background: url('../public/images/tab/tab03.png');
}
.tabBtn04 {
    background: url('../public/images/tab/tab04.png');
}
.phoneWrap {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.contents_top {
    width: 332px;
    height: 86px;
    line-height: 115px;
    text-align: center;
    margin-top: 60px;
    border-radius: 40px 40px 0 0;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
}
.contents_bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 332px;
    height: auto;
    min-height: 620px;
    background: #fff;
    margin-bottom: 60px;
    padding: 20px 15px;
    border-radius: 0 0 40px 40px;
    box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
}
.contents_bottom .messageWrap {
    min-height: 300px;
}
.contents_bottom .textInputWrap {
    position: relative;
}
.contents_bottom .textInputWrap input {
    width: 100%;
    height: 34px;
    background-color: #f1f1f1;
    padding: 5px 10px;
    border: none;
    border-radius: 17px;
    box-sizing: border-box;
}
.contents_bottom .textInputWrap button {
    position:absolute;
    top: 3px;
    right: 3px;
    width: 28px;
    height: 28px;
    border: none;
    border-radius: 14px;
    color: #fff;
    cursor: pointer;
}
.incoming {
    display: flex;
}
.incoming .profileImg, .outgoing .profileImg {
    width: 35px;
    height: 35px;
}
.incoming .message {
    margin-left: 10px;
}
.incoming .message > p {
    color: #fff;
}
.incoming .message span, .outgoing .message span {
    margin: 0 0 5px 0;
}
.incoming .message div span, .outgoing .message div span {
    display: inline-block;
    align-items: flex-start;
    padding: 5px 10px;
    border-radius: 7px;
}
.outgoing {
    display: flex;
    flex-direction: row-reverse;
    text-align: right;
    margin-top: 7px;
    margin-bottom: 20px;
}

/* slytherin */
.slytherin * {
    font-family: 'HarryPotter', 'NanumPenScript', cursive;
}
.slytherin.allWrap {
    background: url(../public/images/background/bg01.png) #000000 center bottom no-repeat;
}
.slytherin .contents_top {
    background: url(../public/images/theme/topImg01.png) no-repeat;
    font-size: 18px;
    color: #fff;
}
.slytherin .contents_bottom {
    background: url(../public/images/theme/messageBg01.png) bottom #000 no-repeat;
}
.slytherin .contents_bottom .incoming .profileImg {
    background: url(../public/images/theme/profile01.png) no-repeat;
}
.slytherin .contents_bottom .incoming span {
    font-size: 15px;
    color: #fff;
    background-color: rgba(0, 94, 75, 0.85);
}
.slytherin .contents_bottom .outgoing span {
    font-size: 15px;
    color: #000;
    background-color: rgba(195, 196, 200, 0.85);
}
.slytherin .contents_bottom .textInputWrap input {
    font-size: 18px;
}
.slytherin .contents_bottom .textInputWrap button {
    background-color: #035d4b;
}

